.search {
    position: absolute;
    top: 35px;
    left: 32%;
    width: 100vw;
}

.search > h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 559px;
    padding: 10px;
    background-color: white;
    position: absolute;
    left: 0;
    top: 349px ;
 }

 .search > input {
     width: 539px;
     padding: 20px;
     position: absolute;
     left: 0;
     height: 30px;
     top: 398px;
     border: none;
     outline-width: 0;
}

.search > button {
    position: absolute;
    left: 0;
    top: 465px;
    text-transform: inherit !important;
    background-color: #ff777f;
    color: white;
    width: 579px;
    border-radius: 0;
}

.search > button:hover {
    background-color: white;
    color: #ff777f;
}